body {
  margin: 0 0 0 0;
  padding: 0 0 0 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root{
  margin-top: 15px;
  margin-left: 15px;
  margin-right: 15px;
  margin-bottom: 15px;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.filter-tags {
  margin-left: 15px;
}

.hide{
  display: none;
}

.show{
  display:block;
}

#bookmarkContainer{
  background-color: azure;
}

.fieldset-padding{
  padding-left: 10px;
  padding-top: 10px;
}
.banner{
  background-color: aqua;
  margin: 0 0 0 0;
  padding: 10px 10px 10px 10px;
  padding-top: 20px;
  padding-left: 10px;
}

.banner h1{
  margin: 0 0 0 0 ;
  padding: 0 0 0 0;

}
.logo{
  height: 30px;
  width: 30px;
}

.header{
  background-color:skyblue;
  margin: 0 0 0 0;
  padding: 0 0 0 0;
  vertical-align: top;
  text-align: left;
  display: -webkit-flex;
  display: flex;
  position: relative;
}

.header a{
  vertical-align: top;
}

.header ul{
  margin: 0 0 0 0;
  margin-left: 10px;
  padding: 5px 0 0 0;
  vertical-align: middle;
}

.header li{
  display: inline;
  vertical-align: middle;
  margin: 0 0 0 0;
  padding: 0 0 0 0;  
  margin-left: 15px;
}

.footer{
  background-color: aquamarine;
}
